import React, { Component } from "react";
import axios from "../../utils/axiosurl";
import pdfImage from "../../assets/pdf.png";

import moment from "moment";
import {
	showYesNoAlert,
	showUnsuccessfulAlert,
	showSuccessfulAlert,
} from "../../services/alertService";
import { TaskType } from "../../backend/types";
import ShowSubmissionButton from "../base/ShowSubmissionButton";

type StateType = {
	file: File | null;
	submitted: boolean;
	success: boolean;
	submitted_file: string;
	github_link: string;
};

class TaskSingle extends Component<TaskType, StateType> {
	constructor(props: any) {
		super(props);
		console.log(props);
		this.state = {
			file: null,
			submitted: false,
			success: false,
			submitted_file: "",
			github_link: "",
		};
	}

	submitFile = () => {
		this.setState(
			{
				submitted: true,
			},
			() => {
				var data = new FormData();

				if (!this.props.is_github) {
					if (this.state.file) {
						data.append("file", this.state.file);
					}
				} else {
					data.append("github_link", this.state.github_link);
				}
				data.append("uuid", this.props.uuid);

				axios
					.post("/course/upload/", data)
					.then(res => {
						if (res.data.status === "successful") {
							showSuccessfulAlert("Successfully Submitted");

							this.setState({
								success: true,
								submitted_file: res.data.file,
							});
						} else {
							showUnsuccessfulAlert(res.data.error);
						}
					})
					.catch(err => {
						alert(err);
						this.setState({
							submitted: false,
						});
					});
			}
		);
	};
	submitHandler = (e: any) => {
		e.preventDefault();
		showYesNoAlert("Are you sure you want to submit?", this.submitFile);
	};

	render_resources = () => {
		if (this.props.resources.length === 0) {
			return <p style={{ paddingBottom: "1rem" }}>No resources to show</p>;
		}
		return this.props.resources.map(item => {
			var start = item.video_link.indexOf("v=");
			var end = item.video_link.length;

			var embed_link = item.video_link.slice(start + 2, end);

			return (
				<div>
					<h4>{item.video_title}</h4>
					<iframe
						height="100%"
						src={"https://www.youtube.com/embed/" + embed_link}
						title="YouTube video player"
						frameBorder="0"
						allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						allowFullScreen
					></iframe>
				</div>
			);
		});
	};

	render_submission = () => {
		//if submitted through form

		if (this.state.success) {
			return (
				<>
					<ShowSubmissionButton
						is_github={this.props.is_github}
						github_link={this.state.submitted_file}
						file={this.state.submitted_file}
					/>
				</>
			);
		}

		//If not submitted show form

		if (this.props.submission === null) {
			return (
				<form onSubmit={e => this.submitHandler(e)}>
					{this.props.is_github ? (
						<input
							type="text"
							id="myFile"
							name="filename"
							placeholder="Enter the link of your GitHub Repository"
							value={this.state.github_link}
							onChange={e => this.setState({ github_link: e.target.value })}
						/>
					) : (
						<input
							type="file"
							id="myFile"
							name="filename"
							onChange={e =>
								this.setState({
									file: e.target.files !== null ? e.target.files[0] : null,
								})
							}
						/>
					)}

					<button
						disabled={
							this.state.submitted ||
							(this.state.file === null && this.state.github_link.length === 0)
						}
						type="submit"
					>
						<span>Submit</span>
					</button>
				</form>
			);
		}
		//else show your submission
		else {
			return (
				<ShowSubmissionButton
					is_github={this.props.is_github}
					github_link={this.props.submission.github_link}
					file={this.props.submission.file}
				/>
			);
		}
	};

	render() {
		return (
			<div className="tasks">
				<div className="task-card-single">
					<div>
						<h1>{this.props.title}</h1>
						<div className="date">
							<p>
								Start Date :{" "}
								<span style={{ fontWeight: 700 }}>
									{moment(this.props.start_date).format(" Do MMMM, dddd")}
								</span>
							</p>

							<p>
								End Date:
								<span style={{ fontWeight: 700 }}>
									{this.props.last_date
										? moment(this.props.last_date).format(" Do MMMM, dddd")
										: "NA"}
								</span>
							</p>
						</div>
					</div>
					<div className="bottom">
						<p>{this.props.description}</p>
						<div className="start-flex-col" style={{ padding: "1rem 0" }}>
							<a
								href={this.props.pdf_file}
								className="bg-contrast pdf-container"
								download
							>
								<img src={pdfImage} alt="pdf_file" /> <span>Download PDF</span>
							</a>
						</div>
						<div>
							<h3>Resources</h3>
							<div className="iframe-container">{this.render_resources()}</div>
						</div>
					</div>

					{this.render_submission()}
				</div>

				{this.props.submission !== null &&
				this.props.submission.comments !== null ? (
					<div className="container task-card-single">
						<h2>Comments</h2>
						<input
							readOnly
							className="comment-box"
							type="text"
							placeholder={this.props.submission.comments}
						/>
					</div>
				) : (
					<></>
				)}
			</div>
		);
	}
}

export default TaskSingle;
