import React from 'react'
import { Link } from "react-router-dom"


export default function DomainCard(props) {

    return (
        <div className="domain-card" >
            <div className="image">
                <img src={props.src || require("../../assets/ml.png").default} alt="domain-img" />
            </div>
            <h1>{props.title}</h1>
            <p>{props.short_description}</p>
            <Link to={{ pathname: `/tasks/${props.slug}`, taskName: props.title }}>Go to tasks</Link>
        </div>
    )
}
